import styled from 'styled-components';
import { COLORS } from '@src/utils/colors';
import { Icon } from '@components/Icon';

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
`;

export const Content = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 10px;
  width: 100%;
  height: fit-content;
  min-height: 100%;
  padding: 10px;
`;

export const NewMessage = styled.div`
  position: absolute;
  cursor: pointer;
  bottom: 20px;
  left: calc(50% - 69px);
  display: flex;
  padding: 4px 8px;
  border-radius: 5px;
  color: ${COLORS.lightGray10};
  background-color: ${COLORS.blue60};
  
  &:hover {
    background-color: ${COLORS.blue70};
  }

  &:active,
  &.active {
    background-color: ${COLORS.blue80};
  }
`;

export const NewMessageWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
`;

export const ScrollableContent = styled.div`
  width: 100%;
  height: 100%;
  overflow-y: auto;
  position: relative;

  &::-webkit-scrollbar {
    width: 0;
  }
`;

export const Footer = styled.form`
  display: flex;
  gap: 10px;
  align-items: center;
  padding: 10px;
  border-top: 1px solid ${COLORS.lightGray40};
`;

export const StyledButton = styled.button`
  background: transparent;
`;

export const SendIcon = styled(Icon)`
  cursor: pointer;
  color: ${COLORS.gray60};
  transition: color .3s;
  
  &:hover {
    color: ${COLORS.blue60};
  }
`;

export const Title = styled.div`
  font-weight: 500;
`;

export const Header = styled.div`
  display: flex;
  gap: 10px;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid ${COLORS.lightGray40};
  box-shadow: 0 7px 15px rgba(0,0,0,.05);
`;
