import { FC, useEffect, useLayoutEffect, useMemo, useRef, useState } from 'react';
import { IUser } from '@src/api/Users/models';
import * as ST from './styled';
import { AppLoader, Avatar, AvatarSize, Button, Textarea } from '@src/components';
import { Icon, IconSize } from '@components/Icon';
import { IDeleteMessage, IMessage, ISocketMessage } from '@src/api/Dialogs/models';
import { toastError } from '@src/utils/toast';
import { DialogsApi } from '@src/api/Dialogs/DialogsApi';
import { Message } from '@pages/Dialogs/Message';
import { SocketApi } from '@src/api/SocketApi/SocketApi';
import { MessageEvents } from '@src/api/SocketApi/models';
import { useMessageCallBack } from '@src/hooks';
import { useAuth } from '@providers/AuthProvider';
import { useFormik } from 'formik';

interface IProps {
  dialogId: string;
  user: IUser;
  onBack?: () => void;
}

export const ActiveDialog: FC<IProps> = ({ dialogId, user, onBack }) => {
  const { user: currentUser } = useAuth();
  const [isLoading, setIsLoading] = useState(true);
  const [messages, setMessages] = useState<IMessage[]>([]);
  const scrollRef = useRef<HTMLDivElement | null>(null);
  const [showNewMessage, setShowNewMessage] = useState(false);
  const form = useFormik({
    initialValues: {
      message: ''
    },
    onSubmit: (values) => {
      onSend(values.message)
    }
  });

  const onSend = (message: string) => {
    SocketApi.sendMessage({
      dialogId,
      text: message,
    });
    form.resetForm();
  }

  const getDialog = async () => {
    try {
      setIsLoading(true);
      const res = await DialogsApi.fetchDialogInfo(dialogId);

      setMessages(res.data.messages);
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false);
    }
  }

  useMessageCallBack((message: string) => {
    const parsedMessage = JSON.parse(message) as ISocketMessage;

    if (parsedMessage.event === MessageEvents.MESSAGE && parsedMessage.dialogId === dialogId) {
      setMessages(prev => [...prev, parsedMessage]);

      if (parsedMessage.user.id !== currentUser?.id) {
        SocketApi.readMessage({ messageId: parsedMessage.id });
        if (scrollRef.current) {
          const scroll = scrollRef.current?.scrollHeight - scrollRef.current?.clientHeight - scrollRef.current?.scrollTop
          if (scroll >= 50) {
            setShowNewMessage(true)
          }
        }
      }
    }

    if (parsedMessage.event === MessageEvents.DELETE_MESSAGE) {
      const parsedMessage = JSON.parse(message) as IDeleteMessage;

      if (parsedMessage.dialogId === dialogId) {
        setMessages(prev => prev.filter(it => it.id !== parsedMessage.messageId));
      }
    }
  }, [dialogId, currentUser]);

  const scrollToBottom = () => {
    scrollRef.current?.scrollTo(0, scrollRef.current?.scrollHeight)
  }

  const handleScroll = () => {
    if (scrollRef.current) {
      const scroll = scrollRef.current?.scrollHeight - scrollRef.current?.clientHeight - scrollRef.current?.scrollTop

      if (scroll < 25) {
        setShowNewMessage(false);
      }
    }
  }

  useLayoutEffect(() => {
    if (scrollRef.current) {
      const lastMsg = messages[messages.length - 1];
      const scroll = scrollRef.current?.scrollHeight - scrollRef.current?.clientHeight - scrollRef.current?.scrollTop;

      if (lastMsg && lastMsg.user.id === currentUser?.id) {
        scrollToBottom()
      } else if (scroll < 50) {
        scrollToBottom()
      }
    }

  }, [messages]);

  useLayoutEffect(() => {
    if (!isLoading) {
      scrollToBottom()
    }
  }, [isLoading]);

  useEffect(() => {
    getDialog();
  }, [dialogId]);

  const renderedContent = useMemo(() => {
    if (isLoading) {
      return <AppLoader/>
    }

    if (!messages.length) {
      return 'Нет сообщений'
    }

    return messages.map((it) => <Message key={it.id} message={it} />)
  }, [messages, isLoading]);

  return (
    <ST.Wrapper>
      <ST.Header>
        {onBack && (
          <Button onClick={onBack}>
            <Icon type="arrow-left" />
          </Button>
        )}
        <Avatar
          userId={user.id}
          firstName={user.firstName}
          size={AvatarSize.XXXL}
          lastName={user.lastName}
          src={user.avatar}
        />
        <ST.Title>{user.lastName} {user.firstName} ({user.email})</ST.Title>
      </ST.Header>
      <ST.NewMessageWrapper>
        <ST.ScrollableContent
          ref={scrollRef}
          onScroll={handleScroll}
        >
          <ST.Content>
            {renderedContent}
          </ST.Content>
        </ST.ScrollableContent>
        {showNewMessage && (
          <ST.NewMessage onClick={scrollToBottom}>
            Новое сообщение
          </ST.NewMessage>
        )}
      </ST.NewMessageWrapper>

      <ST.Footer onSubmit={form.handleSubmit}>
        <Textarea
          name="message"
          value={form.values.message}
          onChange={form.handleChange}
          placeholder="Введите сообщение"
          onEnter={() => onSend(form.values.message)}
        />
        <ST.StyledButton>
          <ST.SendIcon type="send" size={IconSize.XL} />
        </ST.StyledButton>
      </ST.Footer>
    </ST.Wrapper>
  );
}
